.footer {
  background: #022869;
  padding-top: 50px;

  .footer-upper {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-bottom: 50px;
  }
}

.footer .container {
  display: flex;
  justify-content: space-between;

}

.footer_title {
  font-family: "Inter-ExtraBold";
  color: #fff;
  margin-bottom: 10px;
  font-weight: 800;
  font-size: 16px;
  line-height: 19px;
}

.footer_nav-wrapper {
  display: flex;
}

.footer_nav-item {
  margin-right: 70px;
}

.footer_nav-item:last-child {
  margin-right: 0;
}

.footer_nav-item a {
  display: block;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

.follow_us-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.partner {
  text-align: center;
}

.partner a img {
  width: 68px;
  height: 68px;
}

/* Нижняя секция */

.footer-logo {
  padding: 15px 0;
  background: #001f4d;
}

@media (max-width: 540px) {
  .footer-upper {
    flex-wrap: wrap;
  }
  .footer_nav-wrapper {
    flex-wrap: wrap;
  }  
}