.auth {
  padding: 0 0 120px;
}

.auth_wrapper {
  max-width: 560px;
  width: 100%;
  margin: 0 auto;
}

.enter_form {
  background-color: #022869;
  border-radius: 10px;
  padding: 45px 98px;
  margin-bottom: 20px;
}

.enter_form h3 {
  font-family: "Inter-ExtraBold";
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #ffffff;
  margin-bottom: 25px;
  text-align: center;
}

.enter_form-wrapper h5 {
  font-family: "Inter-Medium";
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}

.enter_form-wrapper input {
  border-radius: 5px;
  width: 100%;
  height: 41px;
  padding: 0 5px;
  margin-bottom: 25px;
  border: none;
}

.enter_form button {
  display: block;
  margin: 0 auto;
  font-family: "Inter-Medium";
  background: #367af5;
  border-radius: 10px;
  border: none;
  padding: 12px 25px;
  cursor: pointer;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  margin-bottom: 17px;
}

.enter_form a {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #ffffff;
}

.auth_reg {
  text-align: center;
}

.auth_reg span,
.auth_reg a {
  font-family: "Inter-SemiBold";
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
}

.auth_reg a {
  font-family: "Inter-Medium";
  color: #367af5;
}


