.catalog-page {
  margin-bottom: 90px;
  h1 {
    text-align: center;
    width: 100%;
    margin-bottom: 60px;
  }


  .col-3 {
    padding-right: 30px;
  }

  .catalog-item {
    border: 1px solid #000000;
    border-radius: 10px;
    height: 357px;
    margin-bottom: 30px;
    min-width: 264px;
    overflow: hidden;

    &:hover .catalog-links-list {
      display: block;
    }
    &:hover  .img-wrapper {
      display: none;
    }
    &:hover  .catalog-title {
      display: none;
    }


    .catalog-links-list {
      display: none;
      transition: 0.5s;
      padding: 20px 8px;

      a {
        font-family: "Inter";
        font-size: 14px;
        line-height: 20px;
        color: #000000;
        display: block;
        width: 100%;
        border-bottom: 1px solid #808080;

        &:hover {
          color: #367AF5;
          border-bottom: 1px solid #367AF5;
        }
      }
      .catalog-more {
        color: #777777;
        display: inline;
        border-bottom: 1px solid #777777;
      }
    }
    .catalog-title {
      font-family: "Inter";
      font-weight: 500;
      font-size: 24px;
      line-height: 29px;
      text-align: center;
      color: #000000;
      margin-top: 12px;
    }

    .img-wrapper {
      height: 223px;

      img {
        height: inherit;
        width: 100%;
        object-fit: cover;
        padding: 20px 15px 0 15px;
        border-radius: 25px;
      }
    }
  }
}
